<template>
  <div>
    <v-container>
      <!-- <phase-indicator></phase-indicator> -->
      <v-layout class="text-xs-left">
        <v-flex>
          <v-flex><b>Notifications</b></v-flex>
          <v-flex></v-flex>
        </v-flex>
      </v-layout>
      <!-- <v-layout class="text-xs-left">
        <v-flex xs3>Savings</v-flex>
        <v-flex xs9>I have saved {{waterPercentage()}} of water.</v-flex>
        <v-flex><v-btn>Share</v-btn></v-flex>
      </v-layout> -->
      <hr>
      <!--Field Irrigation Schedule Data-->
      <v-layout v-if="irrSchedulelength> 0">
        <v-flex class="text-xs-left" @click="vewSchedule(allNotification.irrigationSchedule)">
          View Today's Irrigation Schedule
        </v-flex>
        <v-flex class="text-xs-right text-md-right">
          <!--<font-awesome-icon icon="eye" v-on:click="vewSchedule(allNotification.irrigationSchedule)" />-->
          <v-btn color="info" flat small v-on:click="vewSchedule(allNotification.irrigationSchedule)"> View </v-btn>
        </v-flex>
      </v-layout>
      <!-- Ends-->
      

      <!--Irrigation Started Status-->
      <v-layout v-for="(notification, index) in allNotification.irrigationstarted" :key="index.id">
        <v-flex class="text-xs-left">
          {{notification.irr.field[0].fieldName}}: Irrigation Started
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove('is', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <!--Irrigation Ended Status-->
      <v-layout v-for="(notification, index) in allNotification.irrigationended" :key="index.id">
        <v-flex class="text-xs-left">
          {{notification.field[0].fieldName}}: Irrigation Ended
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove('ie', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <!--Notification Table Data-->
      <v-layout v-for="(notification, index) in allNotification.notification" :key="index.id">
        <v-flex class="text-xs-left">
          {{notification.fieldcrop.field.fieldName}}: {{ notification.message.message }}
          <span v-if="notification.message.msgcode==='IVIB'">: {{notification.data}} %</span>
          <span v-if="notification.message.msgcode==='IVDB'">: {{notification.data}} %</span>
           <span v-if="notification.message.msgcode==='IEXB'">: {{notification.data}} days on {{new Date(notification.timeStamp).toDateString()}}</span>
          <span v-if="notification.message.msgcode==='IDEB'">: {{notification.data}} days on {{new Date(notification.timeStamp).toDateString()}}</span>
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove('note', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <br>
      <!-- Alerts-->

      <v-layout class="text-xs-left">
        <v-flex xs12><b>Alerts</b></v-flex>
      </v-layout>
      <hr>
      <!--Pump Status-->
      <v-layout v-for="(notification, index) in allNotification.pumpstatus" :key="index.id">
        <v-flex xs1>
          <font-awesome-icon icon="exclamation-circle" color="red"/>
        </v-flex>
        <v-flex class="text-xs-left">PLEASE CHECK !<br>
          Pump is ON: {{notification.irr.field[0].fieldName}}.
          <countdown
            :startTime="new Date(notification.irr.startDateTime)"
            :endTime="new Date(notification.endDateTime)"
            @start_callback="startCallBack('event started')"
            @end_callback="endCallBack('event ended')"
          >
          </countdown>
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove('ps', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <!--Microcontroller Status-->
      <v-layout v-for="(notification, index) in allNotification.mcinfo" :key="index.id">
        <v-flex xs1>
          <font-awesome-icon icon="exclamation-circle" color="red"/>
        </v-flex>
        <v-flex class="text-xs-left">PLEASE CHECK !<br>
          {{notification.mc.mcName}}: {{notification.message}}
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove('mc', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <!--Completion Date-->
      <v-layout v-for="(notification, index) in allNotification.completedate" :key="index.id">
        <v-flex xs1>
          <font-awesome-icon icon="exclamation-circle" color="red"/>
        </v-flex>
        <v-flex class="text-xs-left">PLEASE CHECK !<br>
          {{notification.fieldcrop.fieldName}}: Irrigation ending on {{notification.fieldcrop.endDate}}. Days left: {{notification.extradate}}
        </v-flex>
        <v-flex class="text-xs-left">
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove('cd', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <!--Field Actuator Data-->
      <v-layout v-for="(notification, index) in allNotification.fieldactuator" :key="index.id">
        <v-flex xs1 v-if="notification.fieldactuator.length <= 1">
          <font-awesome-icon icon="exclamation-circle" color="red"/>
        </v-flex>
        <v-flex class="text-xs-left" v-if="notification.fieldactuator.length <= 1">
          No Pump/Valves connected to <b>Farm</b>: {{ notification.farmName }}, <b>Field</b>: {{ notification.fieldName }}
        </v-flex>
        <v-flex class="text-xs-right" v-if="notification.fieldactuator.length <= 1">
          <font-awesome-icon icon="times" v-on:click="remove('fa', index, 1)" />
        </v-flex>
      </v-layout>
      <!-- Ends-->
      <!-- Ends-->
      <v-dialog v-model="isDialog" width="500">
      <v-card>
        <v-card-title class="headline grey lighten-2" primary-title > Irrigation Schedule </v-card-title>
        <v-card-text>
          <v-expansion-panel>
            <v-expansion-panel-content
              v-for="(fc,i) in allNotification.irrigationSchedule"
              :key="i"
            >
              <template v-slot:header>
                <div>{{fc.fc.fieldName}} : Today's Irrigation Schedule</div>
              </template>
              <v-card>
                <v-card-text>
                  <v-layout v-for="(fcis,j) in fc.irrigationSchedule" :key="j">
                    <v-flex>
                      Time: {{new Date(fcis.startDateTime).toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })}} for {{fcis.durationMins}} minutes.
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="isDialog = false" > OK </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-container>
  </div>
</template>

<script>
import navbar from '@/components/useful/NavBar.vue'
import farmhand from '../mixins/farmhandMixin'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import axios from 'axios'
import ECPIndicator from './useful/ElectricityPhaseIndicator'
import localForage from 'localforage'
import countdown from '@/components/useful/Countdown2.vue'

export default {
  name: 'HomePage',
  components: {
    FontAwesomeIcon,
    'phase-indicator': ECPIndicator,
    'nav-bar': navbar,
    'countdown': countdown
  },
  data () {
    return {
      allNotification: [],
      allAlert: [],
      isDialog: false,
      irrSchedulelength: 0
    }
  },
  methods: {
    vewSchedule (is) {
      this.isDialog = true
    },
    getNotification () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        axios({
          method: 'get',
          url: obj.$hostname + '/notification/all',
          headers: {
            deviseid: value.devise_id,
            token: value.token,
            email: value.email
          }
        }).then(response => {
          obj.allNotification = response.data
          obj.irrSchedulelength = obj.allNotification.irrigationSchedule.length
          // var i
          // for (i = 0; i < response.data.fieldactuator.length; i++) {
          //   obj.allNotification.push(response.data.fieldactuator[i])
          // }
          console.log(obj.allNotification)
          obj.getOfflineNotification()
        }).catch(error => {
          console.log(error)
        })
      }).catch(error => {
        console.log(error)
      })
    },
    getOfflineNotification () {
      localForage.setItem('notification', this.allNotification)
    },
    getAlert () {
      axios({
        method: 'get',
        url: '',
        params: { }
      }).then(response => {
        this.allAlert = [{'id': 1, 'time': '7:30 pm', 'matter': 'Irrigation period has ended', 'duration': '1hr', 'crop-name': 'crop-name', 'field': 'field'},
          {'id': 2, 'time': '8:30 pm', 'matter': 'Water delivered', 'duration': '10min', 'crop-name': 'crop-name', 'field': 'field'}]
        this.getOfflineAlert()
      }).catch(error => {
        console.log(error)
        var obj = this
        localForage.getItem('alert').then(function (value) {
          obj.allAlert = value
        }).catch(function (err) {
          console.log(err)
        })
      })
    },
    getOfflineAlert () {
      localForage.setItem('alert', this.allAlert)
    },
    waterPercentage () {
      return '35 ltr'
    },
    remove (type, index, n) {
      switch (type) {
        case 'cd':
          if (n === 0) {
            this.allNotification.completedate.splice(1, 1)
          } else {
            this.allNotification.completedate.splice(index, 1)
          }
          break
        case 'fa':
          if (n === 0) {
            this.allNotification.fieldactuator.splice(1, 1)
          } else {
            this.allNotification.fieldactuator.splice(index, 1)
          }
          break
        case 'ie':
          if (n === 0) {
            this.allNotification.irrigationended.splice(1, 1)
          } else {
            this.allNotification.irrigationended.splice(index, 1)
          }
          break
        case 'is':
          if (n === 0) {
            this.allNotification.irrigationstarted.splice(1, 1)
          } else {
            this.allNotification.irrigationstarted.splice(index, 1)
          }
          break
        case 'mc':
          if (n === 0) {
            this.allNotification.mcinfo.splice(1, 1)
          } else {
            this.allNotification.mcinfo.splice(index, 1)
          }
          break
        case 'note':
          if (n === 0) {
            this.allNotification.notification.splice(1, 1)
          } else {
            this.allNotification.notification.splice(index, 1)
          }
          break
        case 'ps':
          if (n === 0) {
            this.allNotification.pumpstatus.splice(1, 1)
          } else {
            this.allNotification.pumpstatus.splice(index, 1)
          }
          break
        default:
          console.log('Farmhand')
      }
    }
  },
  mixins: [farmhand],
  created () {
    this.token()
    this.getAlert()
    this.getNotification()
  }
}
</script>



