<template>
  <div>
    <v-container >
      <v-layout row wrap class="pb-4 text-xs-left">
        <v-flex xs12>
          <div class="title">Device Setup: </div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="text-xs-left" v-for="(info) in mcDetails" :key="info.mc.mcId">
        <v-flex xs4>
          <div class="">Device Name: </div>
        </v-flex>
        <v-flex >
          <div class="">{{ info.mc.mcName }}</div>
        </v-flex>
        <v-flex xs2>
          <div class="" v-if="info.status"><font-awesome-icon icon="check-circle" color="green"/></div>
          <div class="" v-else><font-awesome-icon icon="exclamation-circle" color="red"/></div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="pb-4 pt-4 text-xs-left">
        <v-flex xs12>
          <div class="title">Farm Details: </div>
        </v-flex>
      </v-layout>
      <div v-for="(info, j) in farmdetails" :key="j">
      <v-layout row wrap class="text-xs-left" >
        <v-flex xs6>
          <div class="">Total Farm Area: </div>
        </v-flex>
        <v-flex xs6>
          <div class="">{{ info.totalFarm }} acres</div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="text-xs-left">
        <v-flex xs6>
          <div class="">Cultivated area: </div>
        </v-flex>
        <v-flex xs6>
          <div class="">{{ info.cultivatedArea }} acres</div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="pb-4 pt-4 text-xs-left">
        <v-flex xs12>
          <div class="title">Main Crops: </div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="text-xs-left">
        <v-flex xs4  v-for="(crop, k) in info.crops" :key="k">
          {{crop.cropName}}
        </v-flex>
      </v-layout>
    </div>
    </v-container>
  </div>
</template>
<script>
import axios from 'axios'
import localForage from 'localforage'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'

var deviseid
var token
var email

export default {
  components: {
    FontAwesomeIcon
  },
  data () {
    return {
      info: [],
      mcDetails: [],
      farmdetails: []
    }
  },
  methods: {
    onPageLoad () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/generalinfo/?farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          console.log(response)
          obj.info = []
          obj.mcDetails = response.data.mcDetails
          obj.farmdetails = response.data.farmdetails
          obj.info.push(obj.mcDetails)
          obj.info.push(obj.farmdetails)
        }).catch(error => { console.log(error) })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.onPageLoad()
  }
}
</script>



