<template>
  <div>
    <please-wait :progress="progress"></please-wait>
    <v-container>

     <!--  <v-alert v-model="alert" dismissible type="success">
        {{msgDisp}}
      </v-alert> -->
      <v-snackbar
      v-model="snackbar"
      :color="colorValue"
      :multi-line="mode === 'multi-line'"
      :timeout="timeout"
      top
      >{{msgDisp}}
    </v-snackbar>

      <div class="text-xs-right">
        <!-- <phase-indicator></phase-indicator> -->
      </div>
      <!-- <div class="mt-3">Field Info:</div> -->
      <v-expansion-panel>
        <v-expansion-panel-content
        v-for="(fieldcrop,i) in fieldcrops"
        :key="i"
        >
        <div slot="header">
          <b>{{fieldcrop.crop.cropName}}</b> |
          {{fieldcrop.field.fieldName}}  |
          <b>{{fieldcrop.irrigationMode === 1?'Automatic':fieldcrop.irrigationMode === 2?'Semi-Automatic':fieldcrop.irrigationMode === 3?'Manual':'Disable'}}</b>
          <p class="pt-2" style="color:green">{{fieldcrop.timer || fieldcrop.autoirrigationstatus ? 'Irrigation is going on' : ' '}}</p>
        </div>
        <v-dialog v-model="dialog" width="500">
          <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>Shut Down</v-card-title>
            <v-card-text>
              Are you sure you want to turn <b>Off</b> all the pumps and valves on this field
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="error" flat @click="closeAll(fieldcrop), dialog = false">Yes</v-btn>
              <v-btn color="success" flat @click="clickNo">No</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-card class="grey lighten-3">
          <v-card-text>
            <v-layout row>
              Crop Name : &nbsp;<b>{{fieldcrop.crop.cropName}}</b>
            </v-layout>
            <v-layout row>
              Field Name : &nbsp;<b>{{fieldcrop.field.fieldName}}</b>
            </v-layout>
            <span v-if="fieldcrop.field.fieldactuator.length >= 2">
            <v-layout row>
              Pump :&nbsp;
              <span v-for="(actuator,k) in fieldcrop.pumps":key="k">
                <span>
                  {{actuator.actuatorName}}
                  <span v-if="k < fieldcrop.pumps.length-1">,&nbsp;</span>
                </span>
              </span>
            </v-layout>
            <v-layout row>
              Valve :&nbsp;
              <span v-for="(actuator,k) in fieldcrop.valves":key="k">
                <span>
                  {{actuator.actuatorName}}
                  <span v-if="k < fieldcrop.valves.length-1">,&nbsp;</span>
                </span>
              </span>
            </v-layout>
            <v-layout row v-show="fieldcrop.irrigationMode === 1?true:false">
              <v-flex xs4>
                Irrigation Schedule
              </v-flex>
              <v-flex xs8 class="text-xs-right">
                <v-btn
                  slot="activator"
                  :color="fieldcrop.irrigationSwitch === 0? 'success':fieldcrop.irrigationSwitch === 1? 'error':fieldcrop.irrigationStatus === 0?'success':'error'"
                  small flat outline dark
                  @click="getDisable(fieldcrop, fieldcrop.irrigationStatus)">
                {{fieldcrop.irrigationSwitch === 0? 'Resume':fieldcrop.irrigationSwitch === 1? 'Pause':fieldcrop.irrigationStatus === 0? 'Resume':'Pause'}}
                </v-btn>
              </v-flex>
            </v-layout>
            <v-layout row v-show="fieldcrop.irrigationMode === 2?true:false">
              <v-flex xs4>
                Add Irrigation <br> Schedule
              </v-flex>
              <v-flex xs8 class="text-xs-right">
                <!-- Button for Turn Actuator -->
                <v-btn slot="activator" color="success" small flat outline @click="getActuatorControl(fieldcrop,false)" :disabled="fieldcrop.timer">Turn On</v-btn>

                <v-btn slot="activator" color="error" small flat outline @click="getActuatorControl(fieldcrop,true)" :disabled="fieldcrop.timer">Turn Off</v-btn>
              <!-- <span v-if="fieldcrop.timerStartTime"> -->
              <span>
                 <!-- {{timerCountDown(fieldcrop.timerStartTime, fieldcrop.timerDuration)}} -->
                 <!-- <v-text-field v-model="showCountDown" v-show=true></v-text-field> -->
                 <span v-if="fieldcrop.timer">
                   <!-- {{fieldcrop.showCountDown}} -->
                   <v-btn color="error" @click="turnOffIrrigation(fieldcrop.timerData)" small flat outline dark >Timer Off</v-btn>
                 </span>
                 <span v-else>
                   <v-btn color="success" @click="getValve(fieldcrop)" small flat outline dark >Timer On</v-btn>
                 </span>
               </span>
               <!-- <v-dialog v-model="isOver" persistent max-width="290">
                  <v-card>
                    <v-card-title class="headline">Irrigation has been over</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="green darken-1" flat @click.native="isOver = false">close</v-btn>
                    </v-card-actions>
                  </v-card>
               </v-dialog> -->
              </v-flex>
            </v-layout>
            <v-layout row wrap class="text-xs-center" v-if="fieldcrop.timer">
              <v-flex>
                <countdown
                  :startTime="fieldcrop.timerStartTime"
                  :endTime="fieldcrop.timerEndTime"
                  @start_callback="startCallBack('event started')"
                  @end_callback="endCallBack('event ended')"
                  :startLabel="'Turing On...'"
                  :endLabel="'Irrigation Started'"
                  :endText="'Irrigation Ended!'"
                >
                </countdown>
              </v-flex>
            </v-layout>
          </span>
          <span v-else>
            <v-layout>
              No&nbsp;<b>pump</b>&nbsp;/&nbsp;<b>value</b>&nbsp;connected to this field.
            </v-layout>
          </span>
          </v-card-text>
        </v-card>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <v-dialog v-model="endTime" width="500">
      <v-card>
        <v-card-title class="headline grey lighten-2" primary-title>
          Set Time Interval for Field : {{fieldName}} & Crop : {{cropName}} irrigation
        </v-card-title>
        <v-card-text class="text-xs-center">
          <v-radio-group v-model="irrigationDuration" row @click="clearDuration('custom')">
              <v-radio
                v-for="(optionValue, i) in durationOption"
                :key="i"
                :label="optionValue.text"
                :value="optionValue.value"
              ></v-radio>
            </v-radio-group>
            <v-text-field
              v-model.number="customDuration"
              label="Custom"
              type="number"
              @click="clearDuration('radio')"
            ></v-text-field>
            <v-btn round color="primary" @click = "irrigation(irrigationDuration, customDuration)" :disabled="!valid">Confirm</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="phaseInfoDailog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">3 phase not avaliable for the pump to start</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="phaseInfoDailog = false">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</div>
</template>
<script>
import navbar from '@/components/useful/NavBar.vue'
import ECPIndicator from '@/components/useful/ElectricityPhaseIndicator'
import axios from 'axios'
import PleaseWait from '../useful/PleaseWait.vue'
import localForage from 'localforage'
import countdown from '@/components/useful/Countdown.vue'

var deviseid
var token
var email
export default {
  computed: {
    getDuration () {
      return `${this.days}d ${this.hours}h ${this.minutes}m ${this.seconds}s`
    }
  },
  name: 'Pump',
  data () {
    return {
      setTime: '',
      getAutoIrrigationStatus: [],
      panel: false,
      mode: '',
      timeout: 5000,
      snackbar: false,
      colorValue: 'success',
      msgDisp: '',
      timerField: [],
      valid: false,
      showCountDown: '',
      countDownDate: new Date(),
      days: 0,
      hours: 0,
      minutes: 0,
      seconds: 0,
      distance: 0,
      // isOver: false,
      progress: false,
      dialog: '',
      validated: '',
      val2: true,
      val1: false,
      labelMA: 'Automatic',
      label: 'off',
      fieldcrops: [],
      fcDisableInfo: [],
      endTime: false,
      phaseInfoDailog: false,
      phaseStatus: '',
      fc: '',
      fieldName: '',
      cropName: '',
      alert: false,
      msg: '',
      actuators: [],
      mcid: '',
      pumpId: '',
      irrigationDuration: '',
      durationOption: [{'id': 1, 'text': '10', 'value': 10}, {'id': 2, 'text': '20', 'value': 20}, {'id': 3, 'text': '30', 'value': 30}, {'id': 4, 'text': '60', 'value': 60}],
      customDuration: '',
      fcis: [],
      timex: null
    }
  },
  components: {
    'phase-indicator': ECPIndicator,
    'nav-bar': navbar,
    'please-wait': PleaseWait,
    'countdown': countdown
  },
  methods: {
    myFuntion (length) {
      return length - 1
    },
    snackBar (color, msg) {
      this.snackbar = true
      this.colorValue = color
      this.msgDisp = msg
    },
    // dummyContrent (response) {
    //   if (response.) {}
    // },
    startCallBack: function (x) {
      console.log(x)
    },
    endCallBack: function (x) {
      this.snackBar('error', 'Irrigation ended')
      console.log(x)
      this.onPageLoad()
    },
    clearDuration (msg) {
      // radio
      if (msg === 'custom') {
        this.valid = true
        this.customDuration = ''
      } else if (msg === 'radio' && this.customDuration >= 0) {
        this.irrigationDuration = ''
        // check textfield data is exist or not
        this.valid = true
      }
    },
    turnOffIrrigation (fc) {
      var obj = this
      console.log(fc)
      axios({
        method: 'post',
        url: this.$hostname + '/fieldcropirrigations/turnoff_extra_irrigation',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'stopirrigation': fc
        }
      }).then(response => {
        this.snackBar('error', 'Irrigation is ended')
        console.log(response)
        clearInterval(obj.timex)
        obj.showCountDown = null
        obj.onPageLoad()
      }).catch(error => { console.log(error) })
    },
    getActuatorControl (fieldcrop, valveStatus) {
      console.log(fieldcrop)
      console.log(valveStatus)
      var obj = this
      obj.fc = fieldcrop
      obj.msg = 'direct actuation'
      if (valveStatus === true) {
        // on (curr state) --> |Turn OFF| (button clicked)
        if (obj.phaseStatus === 0) {
          this.phaseInfoDailog = true
        } else {
          obj.dialog = true
        }
      } else {
        if (obj.phaseStatus === 0) {
          this.phaseInfoDailog = true
        } else {
          // off (curr state) --> |Turn ON| (button clicked)
          obj.changeActuatorStatus(1)
        }
      }
    },
    getDisable (fieldcrop, irrigationStatus) {
      var obj = this
      obj.fc = fieldcrop
      obj.msg = 'irrigation control'
      console.log(fieldcrop)
      console.log(irrigationStatus)
      if (irrigationStatus !== 0 && fieldcrop.irrigationSwitch !== 0) {
        // start (curr state) --> |stop| (button clicked)
        obj.dialog = true
      } else {
        if (fieldcrop.irrigationSwitch === 1) {
        // start (curr state) --> |stop| (button clicked)
          obj.changeIrrigationStatus(0)
        } else {
          // stop (curr state) --> |start| (button clicked)
          obj.changeIrrigationStatus(1)
        }
      }
    },
    changeActuatorStatus (status) {
      console.log('CAS')
      console.log(status)
      console.log(this.fc.fieldCropId)
      this.actuators = []
      var i
      for (i = 0; i < this.fc.field.fieldactuator.length; i++) {
        this.actuators.push(this.fc.field.fieldactuator[i].actuator.actuatorId)
        if (this.fc.field.fieldactuator[i].actuator.actuatorTypeId === 2) {
          this.mcid = this.fc.field.fieldactuator[i].actuator.mcId
          this.pumpId = this.fc.field.fieldactuator[i].actuator.actuatorId
        }
      }
      console.log(this.actuators)
      console.log(this.mcid)
      // console.log('sdlkfjaksld', this.phaseStatus)
      if (this.phaseStatus === 0) {
        this.phaseInfoDailog = true
        this.snackbar = false
      } else {
        axios({
          method: 'post',
          url: this.$hostname + '/fieldactuator/onoffactuator',
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          },
          data: {
            'fieldCropId': this.fc.fieldCropId,
            'valveStatus': status,
            'mcid': this.mcid,
            'actuators': this.actuators,
            'pumpId': this.pumpId
          }
        }).then(response => {
          console.log(response)
          if (status) {
            this.snackBar('success', 'Irrigation will start in 1 minute')
          } else {
            this.snackBar('error', 'Irrigation is ended')
          }
          this.onPageLoad()
        }).catch(error => { console.log(error) })
      }
    },
    changeIrrigationStatus (status) {
      console.log('CIS')
      axios({
        method: 'post',
        url: this.$hostname + '/fieldcropirrigations/irrigationstatus',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'fieldCropId': this.fc.fieldCropId,
          'irrigationSwitch': status
        }
      }).then(response => {
        console.log(response)
        this.onPageLoad()
      }).catch(error => { console.log(error) })
    },
    getValve (fieldcrop) {
      console.log(fieldcrop)
      var obj = this
      this.endTime = true
      obj.fc = fieldcrop
      obj.fieldName = obj.fc.field.fieldName
      obj.cropName = obj.fc.crop.cropName
      if (obj.phaseStatus === 0) {
        this.phaseInfoDailog = true
        this.endTime = false
      }
      // var j = obj.fieldcrops.indexOf(fieldcrop)
      // obj.timer2(fieldcrop.timerDuration, fieldcrop.timerStartTime, j)
      history.pushState(null, null, location.href)
      window.onpopstate = function () {
        history.go(0)
      }
    },
    irrigation (time, msg) {
      var irrigationTime
      if (time !== '') {
        irrigationTime = time
      } else if (msg !== '') {
        irrigationTime = msg
      }
      console.log(irrigationTime)
      var obj = this
      axios({
        method: 'post',
        url: obj.$hostname + '/fieldcropirrigations/extraschedule',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          fieldCropId: obj.fc.fieldCropId,
          status: 1,
          manual: 1,
          durationMins: irrigationTime
        }
      }).then(function (res) {
        obj.snackBar('success', 'Irrigation will start in 10 seconds')
        obj.customDuration = ''
        obj.irrigationDuration = ''
        obj.endTime = false
        obj.alert = true
        obj.onPageLoad()
      })
    },
    clickNo () {
      // this.onPageLoad()
      this.dialog = false
    },
    closeAll () {
      if (this.msg === 'direct actuation') {
        this.changeActuatorStatus(0)
        this.snackBar('error', 'Irrigation is ended')
      } else if (this.msg === 'irrigation control') {
        this.changeIrrigationStatus(0)
      }
    },
    onPageLoad () {
      var obj = this
      // obj.fieldcrops = []
      // obj.fcDisableInfo = []
      // obj.fcis = []
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldactuator/all/?farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          console.log(response)
          obj.fieldcrops = response.data.fieldcrop
          obj.fcDisableInfo = response.data.fieldirrigation
          obj.fcis = response.data.timer
          obj.getAutoIrrigationStatus = response.data.autoirrigationstatus
          // for (var t = 0; t < obj.timerField.length; t++) {
          // console.log(obj.timerField[t].durationMins)
          //   obj.timer(obj.timerField[t].durationMins)
          // }
          var i, j, m, v, p
          for (i = 0; i < obj.fieldcrops.length; i++) {
            v = []
            p = []
            for (j = 0; j < obj.fieldcrops[i].field.fieldactuator.length; j++) {
              if (obj.fieldcrops[i].field.fieldactuator[j].actuator.actuatorTypeId === 1) {
                v.push(obj.fieldcrops[i].field.fieldactuator[j].actuator)
              }
              if (obj.fieldcrops[i].field.fieldactuator[j].actuator.actuatorTypeId === 2) {
                p.push(obj.fieldcrops[i].field.fieldactuator[j].actuator)
              }
            }
            obj.fieldcrops[i].pumps = p
            obj.fieldcrops[i].valves = v
          }
          // var timer = false
          for (i = 0; i < obj.fcDisableInfo.length; i++) {
            for (j = 0; j < obj.fieldcrops.length; j++) {
              if (obj.fieldcrops[j].fieldCropId === obj.fcDisableInfo[i].fieldCropId) {
                if (obj.fcDisableInfo[i].status === '0') {
                  obj.fieldcrops[j].irrigationStatus = 0 // obj.fcDisableInfo[i].status
                  break
                }
              }
            }
          }
          for (i = 0; i < obj.fcis.length; i++) {
            for (j = 0; j < obj.fieldcrops.length; j++) {
              if (obj.fieldcrops[j].fieldCropId === obj.fcis[i].fieldCropId) {
                if (obj.fcis[i].status === '1' || obj.fcis[i].status === '2' || obj.fcis[i].status === '3') {
                  obj.fieldcrops[j].timer = true // obj.fcDisableInfo[i].status
                  obj.fieldcrops[j].timerDuration = obj.fcis[i].durationMins
                  // obj.timer(obj.fcis[i].durationMins)
                  obj.fieldcrops[j].timerStartTime = new Date(obj.fcis[i].startDateTime)
                  obj.fieldcrops[j].timerData = obj.fcis[i]
                  var countDownDate = new Date(obj.fieldcrops[j].timerStartTime)
                  countDownDate.setMinutes(countDownDate.getMinutes() + obj.fieldcrops[j].timerDuration)
                  obj.fieldcrops[j].timerEndTime = countDownDate
                  // obj.timer2(obj.fieldcrops[j].timerDuration, obj.fieldcrops[j].timerStartTime, j)
                  break
                }
              }
            }
          }
          for (m = 0; m < obj.getAutoIrrigationStatus.length; m++) {
            for (j = 0; j < obj.fieldcrops.length; j++) {
              if (obj.fieldcrops[j].fieldCropId === obj.getAutoIrrigationStatus[m].fieldCropId) {
                obj.fieldcrops[j].autoirrigationstatus = true
                // console.log('kdsjfljksd')
                break
              }
            }
          }
          localForage.setItem('fieldcrop', obj.fieldcrops)
          obj.progress = false
        }).catch(error => { console.log(error) })
      }).catch(function (err) {
        console.log(err)
      })
    },
    getStatus () {
      this.phaseStatus = JSON.parse(localStorage.getItem('phasedetail'))
      if (this.phaseStatus[0].status === 'true' && this.phaseStatus[1].status === 'true' && this.phaseStatus[2].status === 'true') {
        this.phaseStatus = 1
      } else {
        // this.phaseInfoDailog = true
        this.phaseStatus = 0
      }
    },
    phaseInfo () {
      var obj = this
      obj.getStatus()
      obj.progress = false
      console.log('this.phaseStatus')
      if (obj.phaseStatus === 0) {
        this.phaseInfoDailog = true
        this.onPageLoad()
        console.log('phase is not avaliable')
      } else {
        this.onPageLoad()
        console.log('phase avaliable')
      }
    }
  },
  created () {
    this.progress = true
    // this.onPageLoad()
  },
  mounted () {
    setTimeout(this.phaseInfo, 1000)
    var setTime = setInterval(() => {
      var hash = window.location.hash
      console.log(hash)
      this.getStatus()
      var a
      if (a === this.phaseStatus) {

      } else {
        a = this.phaseStatus
      }
      if (hash !== '#/farm/pump') {
        console.log('hash')
        clearInterval(setTime)
      }
      console.log('Phase Status: ' + this.phaseStatus)
    }, 10000)
  }
}
</script>
<style scoped>
</style>



