<template>
  <div>
    <please-wait :progress="progress"></please-wait>
    <div class="container">
      <!-- <phase-indicator></phase-indicator> -->
      <div class="text-xs-right">
          <v-menu offset-y>
          <v-btn slot="activator" color="primary" dark>Time Scale</v-btn>
          <v-list>
            <v-list-tile v-for="(item, index) in items" :key="index" @click="chart(item.title)">
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
      </div>
      <div>
        <line-chart :data="chartData1" legend="top"
        :options="chartOptions1" :label="value"
        xtitle="Time" ytitle="kWh"></line-chart>
        <timeline :data="chartData2"
        xtitle="Time" ytitle="Phase"></timeline>
      </div>
    </div>
  </div>
</template>

<script>
import navbar from '@/components/useful/NavBar.vue'
import ECPIndicator from '../useful/ElectricityPhaseIndicator'
import axios from 'axios'
import localForage from 'localforage'
import PleaseWait from '../useful/PleaseWait.vue'

export default {

  components: {
    'nav-bar': navbar,
    'phase-indicator': ECPIndicator,
    'please-wait': PleaseWait
  },
  data () {
    return {
      progress: false,
      showChart1: true,
      showChart2: false,
      showChart3: false,
      items: [
        { title: 'Today' },
        { title: 'Week' },
        { title: 'Month' }
      ],
      chartData1: [],
      chartOptions1: {},
      value: '',
      chartData2: [],
      point: [],
      truePointP1: [],
      truePointP2: [],
      truePointP3: [],
      phase1: [],
      phase2: [],
      phase3: [],
      graph2: []
    }
  },
  methods: {
    chart (event) {
      this.progress = true
      this.pageOnLoad(event)
    },
    pageOnLoad (event) {
      var obj = this
      localForage.getItem('user').then(function (value) {
        axios({
          method: 'get',
          url: obj.$hostname + '/phaseinfo/?timescale=' + event + '&farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: value.devise_id,
            token: value.token,
            email: value.email
          }
        }).then(response => {
          var i
          var cd = []
          var cdd = []
          console.log(response)
          obj.phase1 = response.data.phase1
          obj.phase2 = response.data.phase2
          obj.phase3 = response.data.phase3
          obj.graph2 = response.data.graph2
          if (event === 'Today') {
            obj.chartOptions1 = {
              title: 'Today',
              legend: { position: 'bottom' }
            }
            obj.value = 'Today'
          } else if (event === 'Week') {
            obj.chartOptions1 = {
              title: 'Last 7 Days',
              legend: { position: 'bottom' }
            }
            obj.value = 'Last 7 Days'
          } else if (event === 'Month') {
            obj.chartOptions1 = {
              title: 'Last 30 Days',
              legend: { position: 'bottom' }
            }
            obj.value = 'Last 30 Days'
          }
          for (i = 0; i < obj.graph2.length; i++) {
            cd.push(obj.graph2[i].x, obj.graph2[i].y)
            cdd.push(cd)
            cd = []
          }
          obj.chartData1 = cdd
          cdd = []
          for (i = 0; i < obj.phase1.length; i++) {
            cd.push('Phase 1', new Date(obj.phase1[i].x[0].timeStamp), new Date(obj.phase1[i].y[0].timeStamp))
            cdd.push(cd)
            cd = []
          }
          for (i = 0; i < obj.phase2.length; i++) {
            cd.push('Phase 2', new Date(obj.phase1[i].x[0].timeStamp), new Date(obj.phase1[i].y[0].timeStamp))
            cdd.push(cd)
            cd = []
          }
          for (i = 0; i < obj.phase3.length; i++) {
            cd.push('Phase 3', new Date(obj.phase1[i].x[0].timeStamp), new Date(obj.phase1[i].y[0].timeStamp))
            cdd.push(cd)
            cd = []
          }
          obj.chartData2 = cdd
          obj.progress = false
        }).catch(error => {
          obj.progress = false
          console.log(error)
        })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  mounted () {
    this.chart('Today')
  }
}
</script>



