<template>
  <v-container>
    <vue-mermaid :nodes="chatData" type="graph LR"></vue-mermaid>
  </v-container>
</template>
<script>
// Referece URL : https://vuejsexamples.com/flowchart-of-mermaid-with-vue-componet/

import axios from 'axios'
import localForage from 'localforage'
var deviseid
var token
var email

export default {
  data: () => ({
    chatData: []
  }),
  methods: {
    onPageLoad () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldactuator/farmsetup',
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          console.log(response)
          obj.chatData = response.data.points
        }).catch(error => { console.log(error) })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.onPageLoad()
  }
}
</script>



