<template>
  <div>
    <v-alert v-model="alert" type="success" dismissible>
      Your Data has Successfully been Submitted
    </v-alert>
    <v-container>
      <v-layout>
        <v-flex>
          <v-select :items="cropnames" name="cropname" item-text="cropName" label="Crop Name" v-model="cropselect" v-on:change="selectCrop(cropselect)"></v-select>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex>
          <v-select :items="cropTypes" name="croptype" item-text="cropTypeName" label="Crop Type" v-model="typeselect" v-on:change="selectType(cropTypes,typeselect)"></v-select>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex>
          <v-select :items="fields" name="field" item-text="fieldName" label="Field No." v-model="fieldselect" v-on:change="selectField(fields,fieldselect)"></v-select>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex>
          <v-dialog
            ref="dialogStartDate"
            v-model="modalStartDate"
            :return-value.sync="date"
            persistent
            lazy
            full-width
            width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="date"
                label="Start Date"
                prepend-icon="event"
                readonly
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="date" :min="freeFrom" color="indigo accent-3" scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="modalStartDate = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.dialogStartDate.save(date), endDate()">OK</v-btn>
            </v-date-picker>
          </v-dialog>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex>
          <v-dialog
            ref="dialogEndDate"
            v-model="modalEndDate"
            :return-value.sync="extendEndDate"
            persistent
            lazy
            full-width
            width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="extendEndDate"
                label="End Date"
                prepend-icon="event"
                readonly
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="extendEndDate" :min="date" color="indigo accent-3"  scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="modalEndDate = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.dialogEndDate.save(extendEndDate)">OK</v-btn>
            </v-date-picker>
          </v-dialog>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex>
          <v-select :items="modenames" label="Mode" name="mode" item-text="modeName" v-model="modeselect" v-on:change="selectMode(modenames,modeselect)"></v-select>
        </v-flex>
      </v-layout>
    </v-container>
    <v-btn class="white--text" color="grey darken-3" type="submit" @click="sendData" round large>Confirm</v-btn>
  </div>
</template>
<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import axios from 'axios'
import localForage from 'localforage'
import farmhand from '@/mixins/farmhandMixin'

var deviseid
var token
var email
export default {
  data () {
    return {
      fieldselect: '',
      modeselect: '',
      typeselect: '',
      cropselect: '',
      items: ['foo', 'bar', 'fizz', 'buzz'],
      names: '',
      cropnames: '',
      modenames: [{'irrigationMode': 1, 'modeName': 'Automated'}, {'irrigationMode': 2, 'modeName': 'Semi-automated'}, {'irrigationMode': 3, 'modeName': 'Manual'}],
      types: '',
      duration: '',
      fields: '',
      picker: null,
      cropName: '',
      modeName: 'Automated',
      irrigationMode: 1,
      cropTypeId: '',
      cropTypeName: '',
      cropId: '',
      fieldId: '',
      today: '',
      no: '',
      date: '',
      extendEndDate: '',
      maxExtendEndDate: '',
      alert: false,
      someFormattedDate: '',
      i: '',
      crops: [],
      freeFrom: '',
      days: '',
      enable: true,
      fieldcrop: [],
      cropCurrent: [],
      cropHistory: [],
      modalStartDate: false,
      modalEndDate: false,
      cropTypes: []
    }
  },
  components: {
    FontAwesomeIcon
  },
  mounted () {
    this.dateToday()
  },
  methods: {
    dateToday () {
      var newdate = new Date()
      this.today = this.dateChange(newdate, 0)
      console.log(this.today)
    },
    selectMode (list, mode) {
      var i
      for (i = 0; i < list.length; i++) {
        if (list[i].modeName === mode) {
          this.modeName = list[i].modeName
          this.irrigationMode = list[i].irrigationMode
          break
        }
      }
    },
    selectCrop (crop) {
      this.cropTypeName = ''
      this.no = ''
      this.date = ''
      this.extendEndDate = ''
      var i
      this.cropName = crop
      this.cropTypes = []
      for (i = 0; i < this.types.length; i++) {
        if (this.cropName === this.types[i].cropName) {
          this.cropTypes = this.types[i].cropTypes
          break
        }
      }
      if (this.cropTypes[0].cropTypeName === null) {
        this.cropTypes = []
      }
      console.log(this.cropTypes)
    },
    selectType (list, type) {
      var i
      for (i = 0; i < list.length; i++) {
        if (list[i].cropTypeName === type) {
          this.cropTypeName = list[i].cropTypeName
          this.cropTypeId = list[i].cropTypeId
          this.cropId = list[i].cropId
          break
        }
      }
      this.no = ''
      this.date = ''
      this.extendEndDate = ''
    },
    selectField (list, field) {
      var i
      for (i = 0; i < list.length; i++) {
        if (list[i].fieldName === field) {
          this.freeFrom = list[i].startDate
          this.no = list[i].fieldName
          this.fieldId = list[i].fieldId
          break
        }
      }
      this.date = ''
      this.extendEndDate = ''
      this.enable = false
    },
    reset () {
      this.cropName = ''
      this.cropTypeName = ''
      this.no = ''
      this.date = ''
      this.someFormattedDate = ''
    },
    endDate () {
      var a = 0
      var newdate
      var dr = this.duration
      var days = 0

      for (a = 0; a < dr.length; a++) {
        if (dr[a] !== null) {
          if (dr[a].cropId === this.cropId) {
            days = dr[a].endDay
            break
          }
        }
      }
      var tt = this.date
      if (tt !== '') {
        if (days === null) {
          days = 30
        }
        newdate = new Date(tt)
        this.someFormattedDate = this.dateChange(newdate, days)
        console.log(this.someFormattedDate)
        this.extendEndDate = this.someFormattedDate
        newdate = new Date(this.someFormattedDate)
        this.maxExtendEndDate = this.dateChange(newdate, 30)
        console.log(this.maxExtendEndDate)
        return this.someFormattedDate
      }
    },
    sendData () {
      var endDate = this.someFormattedDate
      var newEndDate = this.extendEndDate
      if (endDate === newEndDate) {
        newEndDate = null
      }
      console.log('CropId : ' + this.cropId)
      console.log('FieldId : ' + this.fieldId)
      console.log('IM : ' + this.irrigationMode)
      console.log(this.date)
      console.log(endDate)
      console.log(newEndDate)
      axios({
        method: 'post',
        url: this.$hostname + '/fieldcrop/create',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'cropId': this.cropId,
          'fieldId': this.fieldId,
          'status': 1,
          'irrigationMode': this.irrigationMode,
          'startDate': this.date,
          'endDate': endDate,
          'newEndDate': newEndDate
        }
      }).then(response => {
        this.extendEndDate = ''
        this.cropId = ''
        this.fieldId = ''
        this.fieldselect = ''
        this.modeselect = ''
        this.typeselect = ''
        this.cropselect = ''
        this.alert = true
        this.reset()
        this.getData()
      }).catch(error => {
        alert('Please fill in all required fields.')
        console.log(error)
      })
    },
    getData () {
      var obj = this
      axios({
        method: 'get',
        url: this.$hostname + '/crops/all?farmId=' + localStorage.getItem('farm'),
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        }
      }).then(res => {
        console.log(res.data)
        obj.names = res.data.names
        obj.cropnames = res.data.names
        // obj.types = [{'cropTypeId': 1, 'cropTypeName': 'Seed'}, {'cropTypeId': 2, 'cropTypeName': 'Sapling'}]
        obj.types = res.data.crops
        obj.duration = res.data.duration
        obj.fields = res.data.field
      }).catch(error => { console.log(error) })
    }
  },
  mixins: [farmhand],
  created () {
    var obj = this
    localForage.getItem('user').then(function (value) {
      deviseid = value.devise_id
      token = value.token
      email = value.email
      obj.getData()
    }).catch(function (err) {
      console.log(err)
    })
  }
}
</script>
<style scoped>
.cropname{
  padding: 10px;
  border: 1px solid black;
  border-radius: 25px;
}
.my-input.v-input .v-input__slot {
  border-radius: 100px;
}
</style>



