<template>
  <div>
    <v-alert v-model="alert" dismissible type="success" width=100px>
      Thank you for your feedback.
    </v-alert>
    <v-alert v-model="alert1" dismissible type="success" width=100px>
      Your end date of irrigation schedule has been extended to {{endDate}}
    </v-alert>
    <br>
    <v-expansion-panel focusable>
      <v-expansion-panel-content v-for="(crop, i) in crops" :key="i">
        <div slot="header" class="text-xs-center" @click="graph(crop, 'Today')">
          <v-layout row wrap justify-space-around>
            <v-flex xs3>
              {{crop.crop.cropName}}
            <!-- </v-flex>
            <v-flex xs2 pl-2> -->
              {{crop.crop.croptypeId === 1?'Seed':crop.crop.croptypeId ===2?'Sapling':''}}
              <!-- <span v-if="crop.crop.croptype">{{crop.crop.croptype.cropTypeName}}</span> -->
            </v-flex>
            <v-flex xs3>
              {{crop.field.fieldName}}
            </v-flex>
            <v-flex xs3>
              {{convertDateFormat(crop.startDate)}}
            </v-flex>
            <v-flex xs3>
              <span v-if="crop.newEndDate === null">
              {{convertDateFormat(crop.endDate)}}</span>
              <span v-else>{{convertDateFormat(crop.newEndDate)}}</span>
              <v-dialog
              ref="dialogxyz"
              v-model="crop.modelEndDate"
              :return-value.sync="crop.endDate2"
              lazy
              full-width
              width="290px"
              >
              <div slot="activator" ><font-awesome-icon icon="calendar-alt" id="icon"/> </div>
              <!-- <v-date-picker v-model="endDate" :min="crop.endDate" :max="crop.extendEndDate" color="indigo accent-3"> -->
              <v-date-picker v-model="endDate" :min="crop.startDate >=new Date().toJSON().slice(0,10)?crop.startDate:new Date().toJSON().slice(0,10)" color="indigo accent-3">
                <v-btn flat color="primary" @click="extendEndDate(crop.fieldCropId,endDate), crop.fieldId = false">OK</v-btn>
              </v-date-picker>
            </v-dialog>
          </v-flex>
        </v-layout>
      </div>
      <span v-if="crop.field.fieldactuator.length >= 2">
      <v-card class="text-xs-center">
          <v-layout row>
            <v-flex>
              Feedback for Irrigation
            </v-flex>
          </v-layout>
          <v-layout>
            <v-card-text>
              <v-slider
                v-model="crop.fieldcropfeedback"
                :tick-labels="ticksLabels"
                :max="6"
                track-color="#ff00a7"
                tick-size="2"
              >
              </v-slider>
            </v-card-text>
          </v-layout>
          <v-layout>
            <v-flex> Irrigation in % {{ticksLabels[crop.fieldcropfeedback]}}</v-flex>
          </v-layout>
          <v-layout>
            <v-flex><v-btn class='submitField white--text' small @click="pushFeedback(i,ticksLabels[crop.fieldcropfeedback])" color="grey darken-3" round>Confirm</v-btn> </v-flex>
          </v-layout>
        </v-card>
        <v-card>
          <div class="text-xs-right">
              <v-menu offset-y>
              <v-btn slot="activator" color="primary" dark>Time Scale</v-btn>
              <v-list>
                <v-list-tile v-for="(item, index) in items" :key="index" @click="graph(crop, item.title)">
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </div>
          <v-card-media>
            <GChart type="ColumnChart"
            :data="chartData"
            :options="chartOptions"/>
          </v-card-media>
        </v-card>
        </span>
        <span v-else>
          <div class="text-xs-center">
            <p>No&nbsp;<b>pump</b>&nbsp;/&nbsp;<b>value</b>&nbsp;connected to this field.</p>
          </div>
        </span>
    </v-expansion-panel-content>
  </v-expansion-panel>
</div>
</template>

<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import navbar from '@/components/useful/NavBar.vue'
import axios from 'axios'
import { GChart } from 'vue-google-charts'
import localForage from 'localforage'
import farmhand from '@/mixins/farmhandMixin'

var deviseid
var token
var email
export default {
  name: 'Water',
  components: {
    'nav-bar': navbar,
    GChart,
    FontAwesomeIcon
  },
  data () {
    return {
      chartData: [],
      chartOptions: {},
      crops: '',
      fci: '',
      items: [
        { title: 'Today' },
        { title: 'Week' },
        { title: 'Month' },
        { title: 'Crop Cycle' }
      ],
      feedback: 3,
      ticksLabels: ['-15', '-10', '-5', '0', '+5', '+10', '+15'],
      alert: false,
      endDate: '',
      menu: false,
      maxExtendEndDate: '',
      alert1: false,
      fciData: [],
      graphPoint: [],
      points: []
    }
  },
  methods: {
    extendEndDate (fcid, date) {
      var obj = this
      obj.menu = false
      console.log(fcid)
      console.log(date)
      if (date) {
        axios({
          method: 'post',
          url: obj.$hostname + '/fieldcrop/extendenddate',
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          },
          data: {
            'fcid': fcid,
            'date': date,
            'farmId': localStorage.getItem('farm')
          }
        }).then(response => {
          obj.alert1 = true
          console.log(response)
          // obj.onPageLoad()
          obj.$router.go(0)
        }).catch(error => {
          console.log(error)
        })
      }
    },
    pushFeedback (i, cropfeedback) {
      var fc = this.crops[i].fieldCropId
      var obj = this
      var date = Date()
      axios({
        method: 'post',
        url: obj.$hostname + '/feedback/create',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'percentChange': cropfeedback,
          'timeStamp': date,
          'fieldCropId': fc,
          'farmId': localStorage.getItem('farm')
        }
      }).then(response => {
        console.log(response)
        obj.alert = true
        // obj.onPageLoad()
      }).catch(error => {
        console.log(error)
      })
    },
    onPageLoad () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/feedback/all?farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          console.log(response.data)
          obj.crops = response.data.data
          var i
          var cLength = obj.crops.length
          for (i = 0; i < cLength; i++) {
            obj.crops[i].fieldcropfeedback = 3
            var newdate = new Date(obj.crops[i].endDate)
            var date = obj.dateChange(newdate, 30)
            obj.crops[i].extendEndDate = date
            obj.crops[i].endDate2 = ''
          }
          // obj.changeFarm()
        }).catch(error => {
          console.log(error)
        })
      }).catch(function (err) {
        console.log(err)
      })
    },
    graph (fc, event) {
      console.log(fc)
      var obj = this
      axios({
        method: 'get',
        url: obj.$hostname + '/feedback/graph?timescale=' + event + '&fieldCropId=' + fc.fieldCropId,
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        }
      }).then(response => {
        console.log(response.data)
        var i
        var cd = []
        var cdd = []
        cd.push('Days', 'Planned', 'Delivered')
        cdd.push(cd)
        cd = []
        obj.fciData = response.data.data
        if (event === 'Today') {
          for (i = 0; i < obj.fciData[0].data.length; i++) {
            cd.push(new Date(obj.fciData[0].data[i].startDateTime).toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true }), obj.fciData[0].data[i].durationMins, obj.fciData[0].deliveryDuration[i].durationMins)
            cdd.push(cd)
            cd = []
          }
          obj.chartData = cdd
          this.chartOptions = {
            title: 'Today',
            legend: { position: 'bottom' }
          }
        } else if (event === 'Week') {
          for (i = 0; i < obj.fciData.length; i++) {
            cd.push(obj.fciData[i].days, obj.fciData[i].duration, obj.fciData[i].deliveryDuration)
            cdd.push(cd)
            cd = []
          }
          obj.chartData = cdd
          this.chartOptions = {
            title: 'Last 7 Days',
            legend: { position: 'bottom' }
          }
        } else if (event === 'Month') {
          var day, length
          length = obj.fciData.length - 1
          for (i = 0; i < length; i++) {
            day = new Date(obj.fciData[i].days).getDate() + '/' + (new Date(obj.fciData[i].days).getMonth() + 1) + '-' + (new Date(obj.fciData[i + 1].days).getDate() - 1) + '/' + (new Date(obj.fciData[i + 1].days).getMonth() + 1)
            cd.push(day, obj.fciData[i].duration, obj.fciData[i].deliveryDuration)
            cdd.push(cd)
            cd = []
          }
          day = new Date(obj.fciData[length].days).getDate() + '/' + (new Date(obj.fciData[length].days).getMonth() + 1) + '-' + (new Date().getDate()) + '/' + (new Date().getMonth() + 1)
          cd.push(day, obj.fciData[length].duration, obj.fciData[length].deliveryDuration)
          cdd.push(cd)
          this.chartData = cdd
          this.chartOptions = {
            title: 'Last 30 Days',
            legend: { position: 'bottom' }
          }
        } else if (event === 'Crop Cycle') {
          var days, lengths
          lengths = obj.fciData.length - 1
          for (i = 0; i < lengths; i++) {
            days = new Date(obj.fciData[i].days).getDate() + '/' + (new Date(obj.fciData[i].days).getMonth() + 1) + '-' + (new Date(obj.fciData[i + 1].days).getDate() - 1) + '/' + (new Date(obj.fciData[i + 1].days).getMonth() + 1)
            cd.push(days, obj.fciData[i].duration, obj.fciData[i].deliveryDuration)
            cdd.push(cd)
            cd = []
          }
          days = new Date(obj.fciData[lengths].days).getDate() + '/' + (new Date(obj.fciData[lengths].days).getMonth() + 1) + '-' + (new Date().getDate()) + '/' + (new Date().getMonth() + 1)
          cd.push(days, obj.fciData[lengths].duration, obj.fciData[lengths].deliveryDuration)
          cdd.push(cd)
          this.chartData = cdd
          this.chartOptions = {
            title: 'Crop Cycle',
            legend: { position: 'bottom' }
          }
        }
      }).catch(error => {
        console.log(error)
      })
    }
  },
  mixins: [farmhand],
  mounted () {
    this.onPageLoad()
  }
}
</script>
<style>
.submitField {
  border-radius: 15px;
  border: 2px solid #8c95a3;
}
</style>



