<template id="countdown-template">
  <div>
    Time left: {{ minutes | two_digits }} mins : {{ seconds | two_digits }} sec
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  mounted () {
    window.setInterval(() => {
      if (this.hr === 0 && this.min === 0 && this.sec === 0) {
        this.$emit('end_callback')
      }
      this.now = Math.trunc((new Date()).getTime() / 1000)
    }, 1000)
  },
  props: {
    startTime: {
      type: Date
    },
    endTime: {
      type: Date
    },
    start_callback: {
      type: Function
    },
    end_callback: {
      type: Function
    },
    startLabel: {
      type: String
    },
    endLabel: {
      type: String
    },
    endText: {
      type: String
    }
  },
  data () {
    return {
      now: Math.trunc((new Date()).getTime() / 1000),
      hr: '',
      min: '',
      sec: ''
    }
  },
  computed: {
    dateInMilliseconds () {
      return Math.trunc(Date.parse(this.endTime) / 1000)
    },
    seconds () {
      this.sec = (this.dateInMilliseconds - this.now) % 60
      return (this.dateInMilliseconds - this.now) % 60
    },
    minutes () {
      this.min = Math.trunc((this.dateInMilliseconds - this.now) / 60) % 60
      return Math.trunc((this.dateInMilliseconds - this.now) / 60) % 60
    },
    hours () {
      this.hr = Math.trunc((this.dateInMilliseconds - this.now) / 60 / 60) % 24
      return Math.trunc((this.dateInMilliseconds - this.now) / 60 / 60) % 24
    }
  }
}

Vue.filter('two_digits', (value) => {
  if (value < 0) {
    return '00'
  }
  if (value.toString().length <= 1) {
    return `0${value}`
  }
  return value
})
</script>



