<template>
  <v-container>
    <vue-mermaid :nodes="chatData" ></vue-mermaid>
  </v-container>
</template>
<script>
// Referece URL : https://vuejsexamples.com/flowchart-of-mermaid-with-vue-componet/
// type="graph LR"

import axios from 'axios'
import localForage from 'localforage'
var deviseid
var token
var email

export default {
  data: () => ({
    chatData: [
      {id: '1', text: 'Farmer', next: ['2', '3', '4', '5']},
      {id: '2', text: 'P1', next: ['6', '7']},
      {id: '3', text: 'P2', next: ['8', '9', '10', '11']},
      {id: '4', text: 'P3', next: ['12', '13', '14']},
      {id: '5', text: 'P4', next: ['15', '16']},
      {id: '6', text: 'V1', next: ['17']},
      {id: '7', text: 'V2', next: ['18']},
      {id: '8', text: 'V3', next: ['19']},
      {id: '9', text: 'V4', next: ['20']},
      {id: '10', text: 'V5', next: ['21']},
      {id: '11', text: 'V6', next: ['22']},
      {id: '12', text: 'V7', next: ['23']},
      {id: '13', text: 'V8', next: ['24']},
      {id: '14', text: 'V9', next: ['25']},
      {id: '15', text: 'V10', next: ['26']},
      {id: '16', text: 'V11', next: ['27']},
      {id: '17', text: 'F1'},
      {id: '18', text: 'F2'},
      {id: '19', text: 'F3'},
      {id: '20', text: 'F4'},
      {id: '21', text: 'F5'},
      {id: '22', text: 'F6'},
      {id: '23', text: 'F7'},
      {id: '24', text: 'F8'},
      {id: '25', text: 'F9'},
      {id: '26', text: 'F10'},
      {id: '27', text: 'F11'}
    ]
  }),
  methods: {
    onPageLoad () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldactuator/farmsetup',
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          console.log(response)
          console.log(obj.chatData)
          // obj.chatData = response.data.points
        }).catch(error => { console.log(error) })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.onPageLoad()
  }
}
</script>



