<template>
  <div>
    <v-layout>
      <v-flex class="text-xs-left">{{todaysTime()}}</v-flex>
      <v-flex class="text-xs-right">Electricity Phase</v-flex>
      <v-flex class="text-xs-right" xs4>
        <strong>
          <span style="font-size: 13px;position:absolute;text-align:center;width:22px;padding-top:3px">
        1</span>
          <font-awesome-icon :color="red" icon="lightbulb" style="font-size: 30px;"/>
        </strong>
        <strong>
          <span style="font-size: 13px;position:absolute;text-align:center;width:22px;padding-top:3px">
          2</span>
          <font-awesome-icon :color="yellow" icon="lightbulb" style="font-size: 30px;"/>
        </strong>
        <strong>
         <span style="font-size: 14px;position:absolute;text-align:center;width:22px;padding-top:3px">
        3</span>
        <font-awesome-icon :color="blue" icon="lightbulb" style="font-size: 30px;"/>
        </strong>

      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import axios from 'axios'
import localForage from 'localforage'

// // Create WebSocket connection.
// const socket = new WebSocket('ws://localhost:3000/phase')
//
// // Connection opened
// socket.addEventListener('open', function (event) {
//   socket.send('Hello Server!')
// })
//
// // Listen for messages
// socket.addEventListener('message', function (event) {
//   console.log('Message from server ', event.data)
// })

/*
var ws = new WebSocket('ws://192.168.43.41:3000/phase')

ws.onmessage = function (e) {
  print(e.data)
}

ws.onopen = function (e) {
  console.log(e)
}

ws.onclose = function (e) {
  console.log(e)
} */
export default {
  components: {
    FontAwesomeIcon
  },
  data () {
    return {
      allPhase: '',
      blue: '',
      red: '',
      yellow: ''
    }
  },
  methods: {
    todaysTime () {
      // var months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
      var time = new Date()
      // var thisMonth = months[time.getMonth()]
      var month = '' + (time.getMonth() + 1)
      var day = '' + time.getDate()
      var year = time.getFullYear()

      if (month.length < 2) month = '0' + month
      if (day.length < 2) day = '0' + day

      // return thisMonth + ' ' + [day, month, year].join('-')
      return [day, month, year].join('-')
    },
    color (phase) {
      if (phase[0].status === 'true') {
        this.red = 'red'
      } else {
        this.red = ''
      }
      if (phase[1].status === 'true') {
        this.yellow = '#FBC02D'
      } else {
        this.yellow = ''
      }
      if (phase[2].status === 'true') {
        this.blue = '#039BE5'
      } else {
        this.blue = ''
      }
    },
    phaseDetails () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        axios({
          method: 'post',
          url: obj.$hostname + '/phase',
          headers: {
            deviseid: value.devise_id,
            token: value.token,
            email: value.email
          },
          data: {
            'farmId': localStorage.getItem('farm')
          }
        }).then(response => {
          console.log(response)
          obj.allPhase = response.data.data
          localStorage.setItem('phasedetail', JSON.stringify(obj.allPhase))
          obj.color(obj.allPhase)
        }).catch(error => {
          console.log(error)
          obj.green = ''
          obj.red = ''
          obj.yellow = ''
        })
      }).catch(error => {
        console.log(error)
      })
    },
    fetchPhaseDetails: function () {
      setInterval(() => {
        this.phaseDetails()
      }, 10000)
    }
    // fetchPhaseDetails: function () {
    //   axios({
    //     method: 'get',
    //     url: this.$hostname + '/phase'
    //   }).then(response => {
    //     this.allPhase = response.data.data
    //     localStorage.setItem('phasedetail', JSON.stringify(this.allPhase))
    //     this.color(this.allPhase)
    //   }).catch(error => {
    //     console.log(error.response)
    //     this.blue = ''
    //     this.red = ''
    //     this.yellow = ''
    //   })
    // }
  },
  created () {
    this.phaseDetails()
    this.fetchPhaseDetails()
  }
}
</script>



